import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexComponent } from './index/index.component';
import { PromoCodesResolver } from './promo-codes.resolver';
import { PromoCodeFormComponent } from './promo-code-form/promo-code-form.component';
import { PromoCodeDetailsComponent } from './promo-code-details/promo-code-details.component';
import { PromoCodeDeleteComponent } from './promo-code-delete/promo-code-delete.component';


const routes: Routes = [
  {
    path: '',
    component: IndexComponent,
    resolve: {
      promocodes: PromoCodesResolver
    },
    children: [
      {
        path: 'create',
        component: PromoCodeFormComponent,
      },
      {
        path: ':id/edit',
        component: PromoCodeFormComponent,
      },
      {
        path: ':id/details',
        pathMatch: 'full',
        component: PromoCodeDetailsComponent,
      },
      {
        path: ':id/delete',
        pathMatch: 'full',
        component: PromoCodeDeleteComponent,
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PromoCodesRoutingModule { }
