import { Component, OnInit, ViewChild } from '@angular/core';
import { PromoCode } from 'src/app/models/promo_code.model';
import { ITableContext, TableViewComponent } from 'src/app/shared/dashboard-resource/table-view/table-view.component';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent {
  data: PromoCode[];
  context: ITableContext<PromoCode> = {
    urls: {
      createUrl: 'create',
      detailsUrl: ':id'
    },
    data: this.data,
    properties: [
      {
        key: 'code',
        title: 'Code',
        type: 'string'
      },
      {
        key: 'type',
        title: 'type',
        type: 'string'
      },
      {
        key: 'start_at',
        title: 'Start At',
        type: 'datetime'
      },
      {
        key: 'finish_at',
        title: 'Finish At',
        type: 'datetime'
      },
    ],
    title: 'Promo codes'
  };
  @ViewChild('table') table: TableViewComponent<PromoCode>;

  constructor(route: ActivatedRoute) {
    // console.log(this.context);
    route.data.subscribe(data => {
      this.data = data.promocodes;
      this.context.data = this.data;
    });
  }
}
