import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

import { environment } from '../../../environments/environment';
import { PromoCode } from '../../models/promo_code.model';
import { Observable } from 'rxjs';

@Injectable()
export class PromoCodesResolver implements Resolve<PromoCode[]> {
    constructor(private http: HttpClient) {
    }

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<PromoCode[]> {
        return this.http.get<PromoCode[]>(`${ environment.apiUrl }/${ environment.promoCodesFetchUrl }`);
    }
}
