import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ResourceFormContent } from 'src/app/shared/dashboard-resource/resource-form/resource-form.component';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal, NgbModalRef, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-promo-code-form',
  templateUrl: './promo-code-form.component.html',
  styleUrls: ['./promo-code-form.component.scss']
})
export class PromoCodeFormComponent extends ResourceFormContent {
  title: string;
  message: string;
  formGroup: FormGroup;
  allowedTypes: { title: string, id: number }[];

  @ViewChild('content') content: ElementRef<HTMLElement>;

  constructor(http: HttpClient, route: ActivatedRoute, router: Router, service: NgbModal) {
    super(http, route, router, service);

    this.formGroup = new FormGroup({
      type: new FormControl(null, Validators.required),
      discount: new FormControl({ value: null, disabled: this.isDiscountDisabled() }, Validators.required),
      start_at: new FormControl(null, Validators.required),
      finish_at: new FormControl(null, Validators.required),
    });

    this.allowedTypes = [
      {
        id: 0,
        title: 'Percentage',
      },
      {
        id: 1,
        title: 'Constant',
      },
    ];
  }

  openModal(): NgbModalRef {
    const options: NgbModalOptions = {
      backdrop: 'static'
    };

    return this.service.open(this.content, options);
  }

  postUrl(): string {
    return 'promoCodesCreateUrl';
  }
  putUrl(): string {
    return 'promoCodesUpdateUrl';
  }

  private isDiscountDisabled(): boolean {
    return this.formGroup?.value?.type;
  }
}
